<template>
  <div class="w-75 m-auto">
    <div v-if="loading">
      <p>Loading data...</p>
    </div>
    <div v-else>
      <div v-if="error">
        <p>{{ error }}</p>
      </div>
      <div v-else>
        <div v-if="newMovies.length > 0" class="container">
          <h2 class="m-xl-2">New movies from 2024:</h2>
          <div class="card-group card-group-scroll w-50">
            <movie-card
              v-for="movie in newMovies"
              :key="movie.id"
              class="col-2 mx-3 my-2"
              :movie="{
                id: movie.id,
                title: movie.title,
                photo_url: movie.photo_url,
                description: movie.description,
                rating: movie.avg_rating,

                director: {
                  first_name: movie.director.first_name,
                  last_name: movie.director.last_name,
                },
              }"
              type="show"
            ></movie-card>
          </div>
        </div>

        <div v-if="newSeries.length > 0" class="container">
          <h2 class="py-2">New series from 2024:</h2>
          <div class="card-group card-group-scroll w-50">
            <movie-card
              v-for="series in newSeries"
              :key="series.id"
              class="col-2 mx-3 my-2"
              :movie="{
                id: series.id,
                title: series.title,
                photo_url: series.photo_url,
                description: series.description,
                rating: series.avg_rating,
                director: {
                  first_name: series.director.first_name,
                  last_name: series.director.last_name,
                },
              }"
              type="tv-show"
            ></movie-card>
          </div>
        </div>
        <p v-if="movies.length === 0 && series.length === 0">
          No data available.
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import MovieCard from "./MovieCard.vue";

export default {
  components: {
    MovieCard,
  },
  data() {
    return {
      loading: true,
      error: null,
      movies: [],
      series: [],
      newMovies: [],
      newSeries: [],
    };
  },


  created() {},

  mounted() {
    this.fetchAllShows();
  },

  methods: {
    fetchAllShows() {
      axios
        .get("http://127.0.0.1:8000/all-shows/")
        .then((response) => {
          this.movies = response.data.movie;
          this.series = response.data.series;
          this.loading = false;
          this.newestMovies();
        })
        .catch((error) => {
          console.error("Error fetching data:", error);
          this.error = "Failed to fetch data due to server error.";
          this.loading = false;
        });
    },

    newestMovies() {
      for (let i of this.movies) {
        // let date_of_release = i.date_of_release
        if (i.date_of_release.split("-")[0] == 2024) {
          this.newMovies.push(i);
        }
      }

      for (let i of this.series) {
        if (i.date_of_release.split("-")[0] == 2024) {
          this.newSeries.push(i);
        }
      }
    },
  },
};
</script>

